$primaryColor: #4527a4;
$secondaryColor: #8344c5;

.bBlocksProModal {
	.components-modal__content {
		>*:last-child {
			max-width: 100%;
		}
	}

	.components-modal__header-heading {
		display: flex;
		align-items: center;
		gap: 25px;

		.upgradeNow {
			margin: 0;
			padding: 8px 15px;
			border-radius: 5px;
		}
	}

	h3 {
		margin: 0 0 15px 0;

		span {
			color: $primaryColor;
		}
	}

	.text {
		font-size: 15px;
		margin: 25px 0 0 0;

		span {
			color: $primaryColor;
		}
	}

	.upgradeNow {
		display: inline-block;
		font-size: 16px;
		font-weight: 500;
		color: #fff;
		background-color: $primaryColor;
		text-decoration: none;
		padding: 12px 20px;
		margin-top: 20px;
		border-radius: 50px;

		&:hover,
		&:focus {
			color: #fff;
			background-color: $secondaryColor;
		}
	}
}