$primaryColor: #4527a4;

span {
    text-transform: capitalize;
    font-size: 14px;
    font-weight: 400;
}


label {
    text-transform: capitalize;
    font-size: 14px;
    font-weight: 400;
}

.ttpTabPanel {
    .sharePanelBody {
        .components-toggle-control {
            margin-top: 15px;
        }

        .shareBtn {
            .components-base-control__field {
                label {
                    margin-right: 20px;
                }
            }
        }
    }
}

.ttpAuthorization {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 10px;
    margin-bottom: 10px;

    button.ttpAuthBtn {
        border: 1px solid $primaryColor;
        box-shadow: 1px 1px 0px 1px $primaryColor;
        color: $primaryColor;
    }

    .wppLoading {
        color: $primaryColor !important;
        font-size: 21px !important;
        font-weight: bold !important;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        align-items: center;
        position: absolute;
        bottom: 0;
        right: 32px;
        transform: translate(-50%, -50%);
        margin: 0;
        z-index: 1;

        .components-spinner {
            margin-top: 0;
            color: $primaryColor;
        }
    }
}



.wp-block-ttp-tiktok-player {

    .ttpUnAuthor {
        background-color: #4527a4;
        padding: 20px;
        text-align: center;
        border-radius: 10px;

        h2 {
            font-size: 30px;
            font-weight: 700;
            color: #fff;
        }
    }
}

.ttpProFeature {
    h3 {
        font-size: 16px;
        font-weight: 500;
    }

    a {
        text-decoration: none;
        font-size: 16px;
        font-weight: 400;
        color: #fff;
        background-color: #4527a4;
        padding: 7px 10px;
        border-radius: 3px;
        display: inline-block;
        margin-top: 5px;
        line-height: normal;

        &:hover {
            color: #fff;
        }
    }
}

.ttpProModal {
    button {
        width: 35px !important;
        height: 35px !important;
        border: 1px solid #B0C4DE;
        border-radius: 3px;


        svg {
            width: 30px;
            height: 30px;
        }
    }
}

.noticePatter {
    text-align: center;
    padding: 5px 5px;
    background: red;
    color: #fff;
    margin-top: 13px !important;
    margin-bottom: 8p
}

.bsbPatters {
    position: relative;
    margin-bottom: 5px;


    img {}

    .overlyButton {
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 100%;
        opacity: 0;
        transition: 0.3s;
        background-color: #1e1b1b94;

        button {
            border: none;
            font-size: 12px;
            border-radius: 2px;
            padding: 4px 15px;
            font-weight: 500;
            text-transform: uppercase;
            cursor: pointer;
            transition: 0.3s;

            &:hover {
                color: #fff;
                background-color: #007cba;
                transition: 0.3s;

            }
        }
    }

    &:hover .overlyButton {
        opacity: 1;
        transition: 0.3s;
    }
}